import React from "react";
import {TestContainer} from "./qanda/Test-container"
import {TestListContainer} from "./testlist/TestListContainer"
import {Form} from "../form/Form"
import json1 from "../../qa/1.json"
import json2 from "../../qa/2.json"
import json3 from "../../qa/3.json"
import json4 from "../../qa/4.json"

import shuffleArray from "../../utils/shuffleArray"
import alphabet from "../../utils/alphabetArray"
export class Container extends React.Component {
    constructor() {
      super()
      this.state = {
          testLink: "",
          testName: "",
          testCount:0,
          page:1,
          testSelected:1,
          startDialog:true,
          chosen: false,
          correct: false,
          character: "",
          question: 0,
          answeredQuestion: [],
          characterArray: [],
          finish: false,
          dialog: false,
          imageLoaded:false,
          imageSrc:"",
          lengthOfTest:32
      }
      this.handleChange = this.handleChange.bind(this)
      this.onAddTestButtonClicked = this.onAddTestButtonClicked.bind(this)
      this.onTestClicked=this.onTestClicked.bind(this)
      this.onPaginationButtonClicked=this.onPaginationButtonClicked.bind(this)
      this.onStartButtonClicked = this.onStartButtonClicked.bind(this);
      this.onAnswerClicked = this.onAnswerClicked.bind(this);
      this.onNextButtonClicked = this.onNextButtonClicked.bind(this);
      this.onRetryButtonClicked = this.onRetryButtonClicked.bind(this);
      this.onContinueButtonClicked = this.onContinueButtonClicked.bind(this);
      this.onEndButtonClicked = this.onEndButtonClicked.bind(this);
      this.onQuestionClicked = this.onQuestionClicked.bind(this);
      this.handleImageLoad=this.handleImageLoad.bind(this);
    }
  
    handleChange(event) {
        const {name, value, type, checked} = event.target
        type === "checkbox" ? this.setState({ [name]: checked }) : this.setState({ [name]: value })
    }
   
    handleJson(json){
      // turn json file into readable test
      let test=[]
      for(let i=0;i<json.length;i++){
        test[i]={}
        test[i].question=json[i].q 
        if(json[i].s){
          test[i].img=json[i].s
        }
        let answers=[]
        answers.push([json[i].a,1])
        for(let ans=0;ans<json[i].n.length;ans++){
          answers.push([json[i].n[ans],0])
        }
        // / random thu tu cau tra loi
        // shuffleArray(answers)
        let returnArray=[]
        let alphabetArray=alphabet()
        for (let o=0;o<answers.length;o++){
          returnArray[o]=[alphabetArray[o],...answers[o]]
        }
        test[i].answers=returnArray
      }
      // shuffleArray(test)
      return test
    }
    componentDidMount(){
      if(!localStorage.getItem("testArray") || !localStorage.getItem("testHistory")){
        localStorage.setItem("testArray", JSON.stringify([]))
        localStorage.setItem("testHistory",0)
      }
      const testArray=JSON.parse(localStorage.getItem("testArray"))
      this.setState({testCount:testArray.length})
    }
    handleImageLoad(){
      let img=document.querySelector(".question-img")
      if(img){
        let imgSrc=document.querySelector(".question-img").getAttribute("src")
        this.setState({imageLoaded:true,imageSrc:imgSrc})
        img.setAttribute("class","question-img")
      }
      
    }
    onRetryButtonClicked() {
      this.setState({
        chosen: false,
        correct: false,
        character: "",
        question: 0,
        answeredQuestion: [],
        characterArray: [],
        finish: false,
        dialog: false
      });
    }
    onContinueButtonClicked() {
      this.setState({ dialog: false });
    }
    onEndButtonClicked() {
      this.setState({ finish: true });
    }
    onQuestionClicked(e) {
      const questionNumber = parseInt(
        e.currentTarget.querySelector(".question-li").innerHTML.substring(0, 2)
      );
      if (this.state.question === this.state.answeredQuestion.length) {
        this.setState(state => {
          state.answeredQuestion.push(2);
          state.characterArray.push("");
          return {
            answeredQuestion: state.answeredQuestion,
            characterArray: state.characterArray
          };
        });
      }
      this.setState(state => {
        state.question = questionNumber - 1;
        if (state.answeredQuestion[state.question] === 1) {
          return {
            question: state.question,
            chosen: true,
            correct: true,
            character: state.characterArray[state.question]
          };
        } else if (state.answeredQuestion[state.question] === 0) {
          return {
            question: state.question,
            chosen: true,
            correct: false,
            character: state.characterArray[state.question]
          };
        } else {
          return { chosen: false, correct: false };
        }
      });
    }
    onAnswerClicked(e) {
      const event = e.currentTarget;
      let character = event.querySelector(".answer__character").innerHTML;
      let isCorrected = event.querySelector(".answer__result").innerHTML;
      //neu answeredquestion của question này ton tai va =2
      //update answeredquestion
      if (this.state.answeredQuestion[this.state.question] === 2) {
        if (isCorrected == 1) {
          this.setState({ chosen: true, correct: true, character });
          this.setState(state => {
            state.answeredQuestion[state.question] = 1;
            state.characterArray[state.question] = state.character;
            return {
              answeredQuestion: state.answeredQuestion,
              characterArray: state.characterArray
            };
          });
        } else {
          this.setState({ chosen: true, correct: false, character });
          this.setState(state => {
            state.answeredQuestion[state.question] = 0;
            state.characterArray[state.question] = state.character;
            return {
              answeredQuestion: state.answeredQuestion,
              characterArray: state.characterArray
            };
          });
        }
      } else {
        if (isCorrected == 1) {
          this.setState({ chosen: true, correct: true, character });
          this.setState(state => {
            state.answeredQuestion.push(1);
            state.characterArray.push(state.character);
            return {
              answeredQuestion: state.answeredQuestion,
              characterArray: state.characterArray
            };
          });
        } else {
          this.setState({ chosen: true, correct: false, character });
          this.setState(state => {
            state.answeredQuestion.push(0);
            state.characterArray.push(state.character);
            return {
              answeredQuestion: state.answeredQuestion,
              characterArray: state.characterArray
            };
          });
        }
      }
    }
    onNextButtonClicked() {
      if (this.state.question + 1 === this.state.lengthOfTest) {
        if (this.state.answeredQuestion.includes(2)) {
          this.setState({ dialog: true });
        } else this.setState({ finish: true });
      } else {
        if (
          !this.state.chosen &&
          this.state.question === this.state.answeredQuestion.length
        ) {
          this.setState(state => {
            state.answeredQuestion.push(2);
            state.characterArray.push("");
            return {
              answeredQuestion: state.answeredQuestion,
              characterArray: state.characterArray
            };
          });
          this.setState(state => {
            return (state.question += 1);
          });
        } else if (
          this.state.question === this.state.answeredQuestion.length - 1 ||
          this.state.answeredQuestion[this.state.question + 1] === 2
        ) {
          this.setState({ chosen: false, correct: false });
          this.setState(state => {
            return (state.question += 1);
          });
        } else {
          this.setState(state => {
            state.question += 1;
            state.character = state.characterArray[state.question];
            if (state.answeredQuestion[state.question] === 1) {
              return {
                correct: true,
                question: state.question,
                character: state.character
              };
            } else {
              return {
                correct: false,
                question: state.question,
                character: state.character
              };
            }
          });
        }
      }
    }
    onPaginationButtonClicked(e){
      const btn = e.target.closest('.page-button');
      if (btn) {
          const goToPage = parseInt(btn.dataset.goto, 10);
          this.setState({page:goToPage})
      }
    }
    onAddTestButtonClicked(){
      const testArray=JSON.parse(localStorage.getItem("testArray"))
      const testHistory=parseInt(localStorage.getItem("testHistory"))
      const testNumber=testHistory+1
      const link=this.state.testLink
      const testName=this.state.testName
      const date= new Date().toLocaleString().split(',')[1].substring(1)
      const info={testNumber,link,testName,date}
      testArray.unshift(testNumber)
      localStorage.setItem(`testNo${testNumber}`, JSON.stringify(info));
      localStorage.setItem("testHistory", testNumber);
      localStorage.setItem("testArray",JSON.stringify(testArray))
      this.setState(state=>{
        return state.testCount+=1
      })
    }
    // onTestClicked(e){
    //   const eventClassName=e.target.className
    //   const testNumber=parseInt(e.currentTarget.querySelector(".test-number").innerHTML)
    //   let testArray=JSON.parse(localStorage.getItem("testArray"))
    //   ////remove test
    //   if(eventClassName.includes("remove-icon")){
    //     this.setState(state=>state.testCount-1)
    //     localStorage.removeItem(`testNo${testNumber}`)
    //     testArray.splice(testArray.indexOf(testNumber), 1 );
    //     localStorage.setItem("testArray",JSON.stringify(testArray))
    //   }
    //   ////select test
    //   this.setState({testSelected:testNumber,startDialog:true})
    //   ////////cap nhat trang thai test dang select la test nao
    //   ////////pass ten va ngay tao vao
    // }

    //-----------demo----------//////
    onTestClicked(e){
      const eventClassName=e.target.className
      const lengthOfTest=this.returnTest().length
      const testNumber=parseInt(e.currentTarget.querySelector(".test-number").innerHTML)
      this.setState({testSelected:testNumber,startDialog:true,finishContainer:false, chosen: false,
        correct: false,
        character: "",
        question: 0,
        answeredQuestion: [],
        characterArray: [],
        finish: false,
        dialog: false,
        lengthOfTest})
    }
    onStartButtonClicked(){
      this.setState({startDialog:false})
    }
    returnTest(){
      if(this.state.testSelected===1){
        return this.handleJson(json1)
      }
      if(this.state.testSelected===2){
        return this.handleJson(json2)
      }
      if(this.state.testSelected===3){
        return this.handleJson(json3)
      }
      if(this.state.testSelected===4){
        return this.handleJson(json4)
      }
    }
    render(){
    const style={
        display:"grid",
        gridTemplateColumns: "1fr 4fr"
    }
    let TEST= this.returnTest()
    
      return(
        <div style={style} className="quiz-container">
          <div>
            
            {/* <Form
              testName={this.state.testName}
              testLink={this.state.testLink}
              handleChange={this.handleChange}
              addTestButton={this.onAddTestButtonClicked}
            ></Form> */}
            <TestListContainer
              page={this.state.page}
              testSelected={this.state.testSelected}
              numResults={this.state.testCount}
              paginationButton={this.onPaginationButtonClicked}
              testCount={this.state.testCount}
              testClicked={this.onTestClicked}
              
            ></TestListContainer>
          </div>
          <TestContainer qAndA={TEST}
                    state={this.state}
                    question={this.onQuestionClicked}
                    answer={this.onAnswerClicked}
                    nextButton={this.onNextButtonClicked}
                    answerButton={this.onAnswerButtonClicked}
                    endButton={this.onEndButtonClicked}
                    continueButton={this.onContinueButtonClicked}
                    handleImageLoad={this.handleImageLoad}
                    testSelected={this.state.testSelected}
                    startButton={this.onStartButtonClicked}
                    startDialog={this.state.startDialog}
                    finishContainer={this.state.finishContainer}
                    retryButton={this.onRetryButtonClicked}
          ></TestContainer>
        </div>
      )
    }
  }