import React from "react";
import {DisplayInformation} from "./Display-information/DisplayInformation"
import {DisplayStartDialog} from "./Dialog/Dialog"
import {Result} from "./Result/Result"
import {Test} from "./Test/Test"
export class TestContainer extends React.Component {
   
    componentDidUpdate(){
      let img=document.querySelector(".question-img")
      if(img){
        let imgSrc=img.getAttribute("src")
        if(this.props.state.imageSrc!==imgSrc){
          img.setAttribute("class","question-img display-question-img")
        } 
      }
     
    }
    
    whatTodisplay(){
      if(!this.props.state.finish && !this.props.startDialog){
        return (
          <Test
            questionButton={this.props.question}
            nextButton={this.props.nextButton}
            answerButton={this.props.answer}
            passState={this.props.state}
            qAndA={this.props.qAndA[this.props.state.question]}
            testLength={this.props.qAndA.length}
            endButton={this.props.endButton}
            continueButton={this.props.continueButton}
            handleImageLoad={this.props.handleImageLoad}
            imageLoaded={this.props.state.imageLoaded}
            imageSrc={this.props.state.imageSrc}
          ></Test>
        )
      } else if(this.props.startDialog){
        return (
          <DisplayStartDialog
            startButton={this.props.startButton}
            testLength={this.props.qAndA.length}
            testSelected={this.props.testSelected}
          ></DisplayStartDialog>
        )
      }
      return (
        <Result
          retryButton={this.props.retryButton}
          passState={this.state}
          testLength={this.props.qAndA.length}
        ></Result>
      )
    }
    render() {
    const informationContainerStyle={
        background:"#fff",
        width:"100%",
        boxShadow: "0px 2px 4px #BEBEBE"}
    const testContainerStyle={
        width:"100%",
        height:"85vh",
        display:"flex",
        justifyContent: "center",
        alignItems: "center"
    }
    const display=this.whatTodisplay()
        return (
          <div style={informationContainerStyle}>
            <DisplayInformation
              passState={this.props.state}
              testLength={this.props.qAndA.length}
              testSelected={this.props.testSelected}
            ></DisplayInformation>
            <div style={testContainerStyle}>
             {display}
            </div>
          </div>
        );
      }
  }